
.containerService {

    width: 90%;
    height: 600px;
    background: blue;
    margin: 0 auto;
    margin-top: 50px;
    background: transparent;
    border-radius: 5px;
    box-shadow: 0 3px 10px rgba(192, 191, 191, 0.19), 0 1px 5px rgba(207, 206, 206, 0.23), 0 1px 5px rgba(207, 206, 206, 0.23); 
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas:
    "primeraCaja segundaCaja terceraCaja cuartaCaja quintaCaja"
    ;

}

.primeraCaja{
    display: block;
    border-right: 1px solid  rgba(192, 191, 191, 0.19);
    height: 80%;
    margin-top: 60px;
   
}

